<template>
    <div class="menu text-left">
        <div class="menu-group" v-for="(item, index) in groupList" :key="index">
            <div class="menu-group__title text-16 MB-20">
                {{item.title}}
            </div>
            <div 
            class="menu-item flex-row-start text-18 text-bold align-center pointer" 
            v-for="(child, index) in item.subs" 
            :key="index"
            :class="{'active': child.path == $route.name}"
            @click="goPath(child.path)">
                <img :src="child.activeIcon" class="menu-item__icon" v-if="child.path == $route.name"/>
                <img :src="child.icon" class="menu-item__icon" v-else/>
                <span>{{child.title}}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
      groupList: Array  
    },
    methods: {
        goPath(name) {
            this.$emit('click', name)
        }
    }
}
</script>

<style lang="scss" scoped>
.menu{
    width: 3.6rem;
    padding: 0 .3rem;
    height: 100%;
    overflow: auto;
    .menu-group{
        // border-bottom: .03rem solid rgba(255, 255, 255, 0.308);
        .menu-group__title{
            color: #898B99;
            margin-top: .3rem;
        }
    }
    .menu-item{
        color: #898B99;
        margin-bottom: .2rem;
        transition: all .3s;
        height: .76rem;
        padding-left: .3rem;
        border-radius: .1rem;
        &.active {
            background: #292820;
             > span{
                color: #898B99;
                background: linear-gradient(-68deg, #978DBE 0%, #FF9E00 0%, #BED400 100%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
        &:hover{
            color: #fff;
            background: #292820;
        }
        .menu-item__icon{
            width: .3rem;
            height: auto;
            margin-right: .2rem;
        }
    }
}
</style>