<template>
    <nav class="nav flex-row-between PR-40">
        <a href="" class="nav-logo flex-row-center">
            <img src="~@/assets/images/common/logo.png" class="nav-logo__pic"/>
        </a>
        <div class="nav-content flex-1 flex-row-end">
            <div class="nav-search flex-1 flex-row-start align-center PL-30 PR-30">
                <el-icon color="#fff" :size="20" class="MR-30">
                    <search />
                </el-icon>
                <input type="text" class="flex-1 text-20 nav-search__input" placeholder="Search market or enter a token access...">
            </div>
            <div class="nav-btn ML-30 text-white flex-row-start align-center pointer">
                <span class="MR-5">Dapp</span>
                <el-icon color="#fff" size=".2rem"><refresh /></el-icon>
            </div>
            <div class="nav-btn ML-30 text-white flex-row-start align-center pointer">
                <img src="~@/assets/images/common/money1.png" class="nav-btn__money"/>
                <span class="ML-10 color-text">$0.757</span>
            </div>
            <div class="nav-btn ML-30 setting text-white flex-row-center align-center pointer">
                <img src="~@/assets/images/common/setting@2x.png" class="nav-btn__money" />
            </div>
            <div class="nav-connect pointer ML-40">
                
                <div class="flex-row-center nav-connect__content">
                    <el-dropdown v-if="getAccount">
                        <div class="el-dropdown-link color-text flex-row-center">
                            <span>{{getAccount}}</span>
                            <!-- <el-icon class="el-icon--right">
                                <arrow-down />
                            </el-icon> -->
                        </div>
                        <template #dropdown>
                        <el-dropdown-menu>
                            <el-dropdown-item @click="logout">Logout</el-dropdown-item>
                        </el-dropdown-menu>
                        </template>
                    </el-dropdown>
                    <div v-else @click="connectHandler" class="flex-row-center">
                        <img v-if="!connectLoading" src="~@/assets/images/common/connect.png" class="nav-connect__icon MR-10"/>
                        <el-icon v-else class="is-loading MR-10" color="#fff" :size="20">
                            <loading />
                        </el-icon>
                        <span class="color-text text-20">Connected</span>
                    </div>
                    
                </div>
            </div>
            <el-icon color="#fff" @click="drawer=true" class="ML-40 pointer nav-menu" :size="20"><Menu /></el-icon>
        </div> 
        
    </nav>
    <el-drawer
        v-model="drawer"
        :show-close="false"
        custom-class="nav-drawer"
        :with-header="false"
    >
        <div class="nav-search flex-1 flex-row-start align-center PL-20 PR-20">
            <el-icon color="#fff" :size="20" class="MR-10">
                <search />
            </el-icon>
            <input type="text" class="flex-1 text-20 nav-search__input" placeholder="Search market or enter a token access...">
        </div>
        <div class="flex-row-start MT-20">
            <div class="nav-btn text-white flex-row-start align-center pointer">
                <span class="MR-5">Dapp</span>
                <el-icon color="#fff" size=".2rem"><refresh /></el-icon>
            </div>
            <div class="nav-btn text-white flex-row-start align-center pointer MR-10 ML-10">
                <img src="~@/assets/images/common/money1.png" class="nav-btn__money"/>
                <span class="ML-10 color-text">$0.757</span>
            </div>
            <div class="nav-btn setting text-white flex-row-center align-center pointer">
                <img src="~@/assets/images/common/setting@2x.png" class="nav-btn__money" />
            </div>
        </div>
        
        <Meun :groupList="groupList" @click="goPath"/>
    </el-drawer>
</template>

<script>
import { CaretBottom, Search, Refresh, Menu, ArrowDown, Loading } from '@element-plus/icons'
import { ElNotification } from 'element-plus'
import TabBar from './tabbar.vue'
import Meun from './Menu.vue';
export default {
    props: {
        groupList: Array
    },
    components: {
        CaretBottom,
        TabBar,
        Search,
        Refresh,
        Menu,
        Meun,
        ArrowDown,
        Loading
    },
    data() {
        return {
            account: '',
            connectLoading: false,
            drawer: false,
        }
    },
    computed: {
        getAccount() {
            if (this.account) {
                return '0x....'+ this.account.substr(-4)
            }
            return ''
        }
    },
    mounted(){
        this.account = this.$wallet.accounts[0]
        this.$wallet.on('accountChange', () => {
            this.account = this.$wallet.accounts[0]
        })
    },
    methods: {
        goPath(name) {
            this.drawer = false
            this.$emit('click', name)
        },
        logout() {
            this.$wallet.logout()
        },
        async connectHandler() {
            if (this.connectLoading) {
                return
            }
            this.connectLoading = true
            try {
                await this.$wallet.initNetwork()
            } catch (error) {
                
            }
            this.connectLoading = false
        },
        /**
         * 跳转到指定页面
         * @param {String} path 页面路由地址
         */
        togglePage(path) {
            if (path == this.$route.path) {
                this.$router.push({
                    path: '/redirect',
                    query: path
                })
                return
            }
            ElNotification({
                title: 'Coming soon',
                message: 'Coming soon',
            })
            return;
            if (!path) {
                return
            }
            this.$router.push({
                path
            })
        }
    }
}
</script>

<style lang="scss">
.nav-drawer{
    background: #101529;
    width: 4rem !important;
    overflow: auto;
    top: 0 !important;
    .el-drawer__body{
        padding: .2rem;
    }
}
</style>
<style lang="scss" scoped>
@media only screen and (max-width: 992px){
    .nav{
        padding-right: .2rem !important;
    }
    .nav-content{
        .nav-menu{
            display: block !important;
            margin-left: .2rem !important;
        }
        .nav-search{
            display: none !important;
        }
        .nav-btn{
            display: none !important;
        }
    }
    
}

.nav-search{
    height: .75rem;
    background: #262520;
    border-radius: .38rem;
    .nav-search__input{
        background: none;
        color: #fff;
    }
}
.nav-btn{
    height: .51rem;
    border: 2px solid #545761;
    border-radius: .26rem;
    padding: 0 .15rem;
    transition: all .3s;
    &.setting{
        width: .51rem;
        padding: 0;
    }
    .nav-btn__money{
        width: .3rem;
        height: .3rem;
    }
    &:hover{
        border-color: #c4c4c4;
    }
}

.nav{
    height: 1.2rem;
    box-shadow: #000000 0 0 10px;
    border-bottom: 1px solid #2C2E35;
    .nav-logo{
        width: 3.6rem;
        .nav-logo__pic{
            width: .8rem;
            height: .8rem;
        }
    }
    
    .nav-connect{
        height: .70rem;
        background: linear-gradient(-68deg, #FFD60D, #DFF522);
        border-radius: .2rem;
        padding: 2px;
        transition: all .3s;
        &:hover{
            transform: scale(.98);
        }
        .nav-connect__content{
            width: 100%;
            height: 100%;
            background: #211F1C;
            border-radius: .2rem;
            padding: 0 .4rem;
        }
    }
    .nav-connect__icon{
        width: .26rem;
        height: .26rem;
    }
    
    .nav-menu{
        display: none;
    }
}
</style>